import React, { useState } from "react";
import { useMoralis, useNFTBalances } from "react-moralis";
import {
  Card,
  Tooltip,
  Modal,
  Skeleton,
  Spin,
  Input,
  Form,
  Image,
  Button,
} from "antd";
import {
  FileSearchOutlined,
  SendOutlined,
  ShoppingCartOutlined,
  CaretUpOutlined,
} from "@ant-design/icons";
import AddressInput from "./AddressInput";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
import DNS from "contracts/swagtag.js";
import { validateSubdomain } from "helpers/validators.js";
import EditMenu from "components/EditMenu.jsx";
const { Meta } = Card;

const styles = {
  NFTs: {
    display: "flex",
    flexWrap: "wrap",
    WebkitBoxPack: "start",
    justifyContent: "flex-start",
    margin: "0 auto",
    maxWidth: "1000px",
    width: "100%",
    gap: "10px",
  },
};

function NFTBalance() {
  const { data: NFTBalances } = useNFTBalances();
  const { Moralis, chainId /*useMoralisWeb3ApiCall, Web3Api*/, account } =
    useMoralis();
  const [visible, setVisibility] = useState(false);
  const [receiverToSend, setReceiver] = useState(null);
  const [nftToSend, setNftToSend] = useState(null);
  const [isPending, setIsPending] = useState(false);
  const { verifyMetadata } = useVerifyMetadata();
  const [isPriceModalVisible, setIsPriceModalVisible] = useState(false);
  const [isEditModalVisible, setIsEditModalVisible] = useState(false);
  const [ip, setIp] = useState();

  const [selected, setSelected] = useState();
  const [domain, setDomain] = useState(window.landed);
  const [price, setPrice] = useState();

  const [config, setConfig] = useState();
  /*const { fetch, data, error, isLoading } = useMoralisWeb3ApiCall(
    Web3Api.account.getNFTsForContract,
    {
      chainId: "0x4",
      address:account,
      token_address: contractAddress,
    }
  }*/

  if(!chainId || !DNS[chainId]) return <></>;
  const { networks, abi } = DNS[chainId];
  const contractAddress = networks[parseInt(chainId)].address;

  async function transfer(nft, receiver) {
    setVisibility(false);
    const options = {
      type: nft.contract_type.toLowerCase(),
      tokenId: nft.token_id,
      receiver: receiver,
      contractAddress: nft.token_address,
    };

    setIsPending(true);

    console.log({options});
    try {
      await Moralis.transfer(options);
      setIsPending(false);
    } catch (e) {
      console.trace(e);
      setIsPending(false);
    }

  }

  const handleTransferClick = (nft) => {
    setNftToSend(nft);
    setVisibility(true);
  };

  const claim = async (base) => {
    setIsPending(true);
    let name = base;
    if (!validateSubdomain) {
      alert("invalid swagtag, please stick to plan text only");
      return;
    }
    const out = {
      contractAddress,
      functionName: "mintToken",
      abi,
      params: { _name: name },
      msgValue: parseInt(10000000000000000),
    };
    try {
      await Moralis.executeFunction(out);
    } catch (e) {
      console.trace(e);
    }
    setIsPending(false);
  };

  const sell = async () => {
    const _price = parseInt(price * 1000000000000000000).toString();

    setIsPending(true);
    setIsPriceModalVisible(false);
    const out = {
      contractAddress,
      functionName: "openTrade",
      abi,
      params: { _item: selected.token_id, _price },
    };
    console.log({ out });
    try {
      await Moralis.executeFunction(out);
    } catch (e) {
      console.trace(e);
    }
    setIsPending(false);
  };
  const bump = async (nft) => {
    let name;
    const params = { id: nft.token_id };
    console.log(await Moralis.Cloud.run("Bump", params));
  };
  const approve = async () => {
    setIsPending(true);
    setIsPriceModalVisible(false);
    const out = {
      contractAddress,
      functionName: "setApprovalForAll",
      abi,
      params: { operator: contractAddress, approved: true },
    };
    console.log({ out });
    try {
      await Moralis.executeFunction(out);
    } catch (e) {
      console.trace(e);
    }
    setIsPending(false);
  };
  const showPriceModal = async (nft) => {
    const data = JSON.parse(
      await Moralis.executeFunction({
        contractAddress,
        functionName: "isApprovedForAll",
        abi,
        params: { owner: account, operator: contractAddress },
      })
    );
    console.log({ data });
    if (
      !data &&
      window.confirm("Would you like to approve the swagtag marketplace?")
    ) {
      approve();
    } else {
      setSelected(nft);
      setIsPriceModalVisible(true);
      console.log("showing price modal");
    }
  };
  const drawnft = (nft, index) => {
    let domain;
    if (chainId === "0xa869") domain = "https://domains.fujiavax.ga/";
    if (chainId === "0xa86a") domain = "https://domains.avax.ga/";
    //if (!nft.token_uri.startsWith('https://domains.avax.ga/') ||) return;
    //if (nft.token_uri.includes("#")) return;
    if (!nft.token_uri) return null;
    let link = nft.token_uri.toString().replace(domain, "");
    if (!link.length) return;
    if (!link) return;
    if (nft.token_address.toLowerCase() !== contractAddress.toLowerCase())
      return null;
    nft = verifyMetadata(nft);
    console.log(nft);
    
    return (
      <Card
        hoverable
        style={{
          width: 240,
          border: "2px solid #e7eaf3",
        }}
        actions={[
          isPending && selected?.token_uri === nft?.token_uri ? (
            <div></div>
          ) : (
            <Tooltip title="Edit">
              <FileSearchOutlined
                onClick={async () => {
                  setSelected(nft);
                  let data = { ips: [] };
                  setIsPending(true);
                  try {
                    data = await Moralis.executeFunction({
                      contractAddress,
                      functionName: "getAddress",
                      abi,
                      params: {
                        _name: nft?.token_uri
                          .replace("https://domains.avax.ga/", "")
                          .replace("https://domains.fujiavax.ga/", ""),
                      },
                    });
                    console.log(data);
                    data = data.length ? JSON.parse(data) : {};
                  } catch (e) {
                    console.log(e);
                  }
                  if (!data.ips) data.ips = [];
                  setConfig(data);
                  setIsPending(false);

                  setIsEditModalVisible(true);
                }}
                style={{ display: isPending ? "none" : "block" }}
              />
            </Tooltip>
          ),
          isPending && selected?.token_uri === nft?.token_uri ? (
            <div style={{ textAlign: "center" }}>
              <Spin />
            </div>
          ) : (
            <Tooltip title="Transfer NFT">
              <SendOutlined
                style={{ display: isPending ? "none" : "block" }}
                onClick={() => handleTransferClick(nft)}
              />
            </Tooltip>
          ),
          isPending && selected?.token_uri === nft?.token_uri ? (
            <div></div>
          ) : (
            <Tooltip title="Sell On Marketplace">
              <ShoppingCartOutlined
                style={{ display: isPending ? "none" : "block" }}
                onClick={() => {
                  showPriceModal(nft);
                }}
              />
            </Tooltip>
          ),
          isPending && selected?.token_uri === nft?.token_uri ? (
            <div></div>
          ) : (
            <Tooltip title="Bump">
              <CaretUpOutlined
                style={{ display: isPending ? "none" : "block" }}
                onClick={() => {
                  bump(nft);
                }}
              />
            </Tooltip>
          ),
        ]}
        cover={
          <Image
            preview={false}
            src={nft?.image || "error"}
            fallback="data:image/png;base64,"
            alt=""
            style={{ height: "300px" }}
          />
        }
        key={index}
      >
        <Meta title={<a href={"https://" + link + "." + ((chainId === "0xa869")?"fuji":"")+"avax.ga"}>{link}</a>} />
      </Card>
    );
  };

  const edit = async () => {
    setIsEditModalVisible(false);
    setIsPending(true);
    const config = Object.assign({}, ip);
    config.uri = selected?.token_uri;
    console.log(config);
    try {
      await Moralis.executeFunction({
        contractAddress,
        functionName: "setAddress",
        abi,
        params: {
          _name: selected?.token_uri
            .replace("https://domains.avax.ga/", "")
            .replace("https://domains.fujiavax.ga/", ""),
          _address: JSON.stringify(config),
        },
      });
    } catch (e) {
      console.trace(e);
    }
    setIsPending(false);
  };

  return (
    <div
      style={{
        margin: "auto",
        display: "flex",
        gap: "20px",
        marginTop: "25",
        width: "70vw",
      }}
    >
      <Card
        size="large"
        style={{
          width: "100%",
          boxShadow: "0 0.5rem 1.2rem rgb(189 197 209 / 20%)",
          border: "1px solid #e7eaf3",
          borderRadius: "0.5rem",
        }}
      >
        <Form
          layout="vertical"
          onSubmit={() => {
            claim(domain);
          }}
          name="claim swagtag"
        >
          <Form.Item required style={{ marginBottom: "15px" }} key="claim">
            <div
              style={{
                margin: "auto",
                display: "flex",
                gap: "20px",
                marginTop: "25",
              }}
            >
              <Input
                value={domain}
                onChange={(e) => {
                  setDomain(e.target.value);
                }}
                placeholder="your swagtag like davinci or legolas"
              />
              <Button
                type={isPending ? "secondary" : "primary"}
                disabled={isPending}
                onClick={() => {
                  claim(domain);
                }}
                htmlType="submit"
              >
                Claim
              </Button>
            </div>
          </Form.Item>
        </Form>
        <div style={styles.NFTs}>
          <Skeleton loading={!NFTBalances?.result}>
            {NFTBalances?.result && NFTBalances.result.map(drawnft)}
          </Skeleton>
        </div>
        <Modal
          title={`Transfer ${nftToSend?.name || "NFT"}`}
          visible={visible}
          onCancel={() => setVisibility(false)}
          onOk={() => transfer(nftToSend, receiverToSend)}
          confirmLoading={isPending}
          okText="Send"
        >
          <AddressInput
            autoFocus
            placeholder="Receiver"
            onChange={setReceiver}
          />
        </Modal>
        <Modal
          title="Sell"
          visible={isPriceModalVisible}
          disabled={isPending}
          onOk={async () => {
            if (!isPending) sell();
          }}
          onCancel={() => {
            setIsPriceModalVisible(false);
          }}
        >
          {isPending ? (
            <Spin />
          ) : (
            <Input
              value={price}
              onChange={(e) => setPrice(e.target.value)}
              placeholder="avax price"
            />
          )}
        </Modal>
        {isEditModalVisible ? (
          <EditMenu
            config={config}
            nft={selected}
            setIp={setIp}
            visible={isEditModalVisible}
            pending={isPending}
            edit={edit}
            close={() => {
              setIsEditModalVisible(false);
            }}
          />
        ) : null}
      </Card>
    </div>
  );
}

export default NFTBalance;
